.user {
  @media (max-width: 1339px) {
    margin-right: 40px;
  }

  @media (max-width: 767px) {
    display: none;
  }
}

.user__link {
  display: flex;
  align-items: center;
}

.user__avatar {
  border-radius: 50%;
  margin-right: 15px;
}

.user__name {
  font-size: 16px;
  line-height: 19px;

  text-decoration: none;
  color: #ffffff;
}
